.vote {

  margin: 0;
  padding: 0;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  border: none;
  background-color: var(--colorWhiteThird);
  box-shadow: var(--elementShadow);
  transition: var(--boxTransition);
  animation: var(--aniBeat);

  &:active {
    transform: scale(.9);
    box-shadow: 0 0px 17px rgba(68, 68, 68, .7);
  }
}

.inner {
  margin: 0 auto;
  padding: 0;
  width: 48px;
  height: 48px;
  border-radius: 50%;
  background: var(--colorPrimary);
  color: var(--colorWhite);
  border: 3px solid var(--colorPrimaryLight);
  display: flex;
  align-items: center;
  justify-content: center;

  span {
    width: 100%;
    text-align: center;
    user-select: none;
    font: var(--textButtonVote);
  }
}